import * as React from "react"
import { BannerModuleStyles } from "./BannerModuleStyles"
import { GatsbyImage } from "gatsby-plugin-image"
import { Grid } from "@mui/material"
import Button from "../Button/Button"
import { navigate } from "gatsby"
import { currentArtist, isNullOrUndefined } from "../../constants/globals"

const BannerModule = ({ children, title, subTitle, learnMoreTitle, learnMoreTarget, bgImage }) => {
    function scrollToArea(target) {
        // noinspection JSIgnoredPromiseFromCall
        navigate(target)
    }

    let _subtitle = ""
    if (!isNullOrUndefined(subTitle)) {
        if (typeof subTitle === "string") _subtitle = <h2>{subTitle}</h2>
        else _subtitle = subTitle
    }

    return (
        <>
            <BannerModuleStyles>
                {bgImage ? (
                    <GatsbyImage
                        className="banner__image"
                        imgClassName="banner__image--content"
                        alt={title + " - Banner"}
                        image={bgImage}
                    />
                ) : (
                    <GatsbyImage
                        className="banner__image"
                        imgClassName="banner__image--content"
                        alt={currentArtist().name + " - Banner"}
                        image={currentArtist().metadata.image.bgImage}
                    />
                )}

                <div className="container">
                    <div className="banner__content">
                        <Grid container spacing={8}>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                {title && (
                                    <h1>
                                        {title}
                                        <span style={{ color: "var(--primary)" }}>.</span>
                                    </h1>
                                )}
                                {_subtitle}
                                {learnMoreTarget && (
                                    <div className="banner__btns">
                                        <Button
                                            onClick={(e) => scrollToArea(learnMoreTarget)}
                                            text={learnMoreTitle || "Learn More"}
                                        />
                                    </div>
                                )}
                            </Grid>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                {children && children}
                            </Grid>
                        </Grid>
                    </div>
                </div>
                <div className="gradient"></div>
            </BannerModuleStyles>
            <span id="topContent"></span>
        </>
    )
}

export default BannerModule
